/*
=============== 
Variables
===============
*/

:root {
/* Colors */	
--light-pink: #ffdede;
--dirty-pink: #d4b8b8;
--dark-red: #5c0303;
--lavender: #E6e6fa;
--bright-red: #ff6366;
--light-beige: #f5e8d0;
--beige-yellow: #f7c568;
--dark-grey: #545252;
--white: #ffffff;
--black: #000000;	
/* Misc */
--transition: all 0.3s linear;
--spacing: 0.1rem;
--radius: 0.25rem;
--light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
--dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
--max-width: 1170px;
--fixed-width: 620px;

}

/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family:'Petrona', serif;
	background: var(--light-pink);
	color: var(--dark-grey);
	line-height: 1.5;
	font-size: 0.875rem;
}

ul {
	list-style-type: none;
}

a {
	text-decoration: none;
	color: var(--dark-red);
}

button {
	color: var(--white);
	border: none;
	background: var(--bright-red);
	font-family: inherit;
	line-height: inherit;
	padding: 0.5rem 2rem;
	border-radius: var(--radius);
	box-shadow: var(--light-shadow);
	text-transform: capitalize;
	letter-spacing: var(--spacing);
	transition: var(--transition);
	cursor: pointer;
}

button:hover {
	background: var(--dark-red);
}

h1, h2, h3, h4 {
	letter-spacing: var(--spacing);
	text-transform: capitalize;
	line-height: 1.25;
	margin-bottom: 0.75rem;
}

h1 {
	font-size: 3rem;
}

h2 {
	font-size: 2rem;
}

h3 {
	font-size: 1rem;
}

h4 {
	font-size: 0.875rem;
}

p {
	margin-bottom: 1.25rem;
}

@media screen and (min-width: 800px) {
	h1 {
		font-size: 4rem;
	}

	h2 {
		font-size: 2.5rem;
	}

	h3 {
		font-size: 1.75rem;
	}

	h4 {
		font-size: 1rem;
	}
	body {
		font-size: 1rem;
	}

	h1, h2, h3, h4 {
		line-height: 1;
	}
}

.section {
	width: 90vw;
	margin: 0 auto;
	max-width: var(--max-width);
}

@media screen and (min-width: 992px) {
	.section {
		width: 95vw;
	}
}

/*
=============== 
Navbar
===============
*/

nav {
	background: var(--white);
	box-shadow: var(--light-shadow);
}

.nav-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 1rem;
}

.nav-toggle {
	font-size: 1.5rem;
	padding: 8px;
	color: var(--dark-red);
	background: transparent;
	border-color: transparent;
	transition: var(--transition);
	cursor: pointer;
}

.nav-toggle:hover {
	color: var(--dirty-pink);
	transform: rotate(90deg);
}

.logo {
	height: 50px;
}

.links a {
	color: var(--dark-grey);
	font-size: 1rem;
	text-transform: capitalize;
	letter-spacing: var(--spacing);
	display: block;
	padding: 0.5rem 1rem;
	transition: var(--transition);
}

.links a:hover {
	background: var(--beige-yellow);
	padding-left: 1.5rem;
}

.social-icons {
	display: none;
}

.links-container {
	height: 0;
	overflow: hidden;
	transition: var(--transition);
}

.show-container {
	height: 10rem;
}

@media screen and (min-width: 800px) {

	.nav-center {
		max-width: var(--max-width);
		margin: 0 auto;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 1rem;
	}

	.nav-header {
		padding: 0;
	}

	.nav-toggle {
		display: none;
	}

	.links-container {
		height: auto !important;
	}

	.links {
		display: flex;
	}
	.links a {
		padding: 0;
		margin: 0 0.5rem;
	}

	.links a:hover {
		padding: 0;
		background: transparent;
	}

	.social-icons {
		display: flex;
	}

	.social-icons a {
		margin: 0 0.5rem;
		color: var(--dark-grey);
		transition: var(--transition);
	}

	.social-icons a:hover {
		color: var(--dirty-pink);
	}
}

/*
=============== 
Hero
===============
*/

.hero {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 2rem;

}

.rec-interaction {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 2rem;
}

.hero-btns {
	margin: 2rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.book-results {
	display: flex;
	align-items: center;
	justify-content: center;
}

.categories {
	background: var(--dirty-pink);
	border: none;
	border-radius: var(--radius);
	padding: 1rem;
	min-height: 300px;
	max-width: 200px;
	color: var(--white);
}

.book {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	background: var(--white);
	border: none;
	border-radius: var(--radius);
	padding: 1rem;
}

.book img {
	width: 50%;
	border-radius: var(--radius);
	padding: 1rem;
}

.book-details {
	padding: 1rem;
}

.link-btn {
	background: none;
	border: none;
	padding: 0;
	box-shadow: none;
	margin: 0;
	color: var(--bright-red);
}

.link-btn:hover {
	color: var(--dark-red);
}

.rec-buttons {
	display: flex;
	align-items: center;
	justify-content: space-around;
	max-width: 350px;
}

.rec-buttons button {
	background: none;
	border: 1px solid var(--dirty-pink);
	color: var(--dark-grey);
	transition: var(--transition);
}

.rec-buttons button:hover {
	background: var(--dirty-pink);
	border: 1px solid var(--dirty-pink);
	color: var(--white);
}

.filter-btn {
	display: block;
	background: var(--white);
	color: var(--dark-grey);
	padding: 0.5rem;
	font-size: 1rem;
	line-height: 1rem;
	margin-right: 1rem;
}

@media screen and (min-width: 600px) {
	.book {
		flex-direction: row;
		align-items: flex-start;
	}
}


@media screen and (min-width: 800px) {
	.filter-btn {
		display: none;
	}

	.book-results {
		display: grid;
		grid-template-columns: 20% 80%;
		grid-column-gap: 2rem;
		align-items: flex-start;
	}

	.book {
		flex-direction: row;
		margin-right: 2rem;
	}

}

/*
================= 
Popup Categories
=================
*/
.popup-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	z-index: 10;
	display: grid;
	place-items: center;
	transition: var(--transition);
}
.popup-categories {
	width: 90vw;
	height: 30vh;
	max-width: var(--fixed-width);
	position: relative;
	background: var(--white);
	border-radius: var(--radius);
	padding: 1rem;
}

.popup-title {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
}

/*
================= 
Eros Interaction
=================
*/

.heart {
  background-color: var(--dark-red);
  display: inline-block;
  height: 50px;
  margin: 0 10px;
  position: relative;
  top: 0;
  transform: rotate(-45deg);
  position: absolute; 
  left: 75%; top: 45%;
  width: 50px;
  animation: heartbeat 1s infinite;
  animation-delay: 5s;
}

.heart:before,
.heart:after {
  content: "";
  background-color: var(--dark-red);
  border-radius: 50%;
  height: 50px;
  position: absolute;
  width: 50px;

}

.heart:before {
  top: -25px;
  left: 0;
}

.heart:after {
  left: 25px;
  top: 0;
}

@keyframes heartbeat {
  0% {
    transform: rotate(-45deg) scale( 1 )   
  }
  20% {
    transform: rotate(-45deg) scale( 1.25 ) 
      translateX(5%) 
      translateY(5%);
  } 
  40% {
    transform: rotate(-45deg) scale( 1.5 ) 
      translateX(9%) 
      translateY(10%);
  }
}

.eros {
	height: 250px;
	position: absolute;
  top: 40%; left: 5%;
  animation: eros-hover 5s 1;
}

.arrow {
	height: 20px;
	transform: rotate(225deg);
	position: absolute;
	top: 43%; left: 68%; 
	animation: arrow-hover 5s 1;
	clip-path: ellipse(75% 100% at 100% 100%);
	z-index: 1;
}

@keyframes eros-hover {
	 0% { 
	 top: 45%; left: -20%;
  }
  25% {
  	top: 55%; left: 2%;
  } 
  50% {
 	top: 35%; left: 5%;
  }
}

@keyframes arrow-hover {
	 0% { 
	 top: 53%; left: -2%; transform: rotate(135deg); clip-path: none;
  }
  25% {
  	top: 63%; left: 20%; transform: rotate(135deg); clip-path: none;
  } 
  50% {
 	top: 43%; left: 23%; transform: rotate(135deg); clip-path: none;
  }
  75% {
  	top: 33%; left: 43%; transform: rotate(170deg); clip-path: none;
  }
  90% {
  	top: 40%; left: 63%; transform: rotate(220deg); clip-path: none;
  }
  100% {
  	top: 43%; left: 68%; transform: rotate(225deg); clip-path: ellipse(75% 100% at 100% 100%);
  }
}

/*
================= 
Lists
=================
*/

.lists {
	margin: 0 auto;
	display: grid;
	grid-template-columns: 50% 50%;
	justify-content: center;
	align-content: center;
	max-width: var(--max-width);
	grid-gap: 1rem;

}

.list {
	background: var(--white);
	border-radius: var(--radius);
	padding: 1rem;
}

/*
================= 
Shelves
=================
*/

.shelves {
	padding: 1rem;
	margin: 0 auto;
	display: flex;
	align-items: center;
	justify-content: center;
	max-width: var(--max-width);
}

.shelves-img {
	width: 25%;
}

.shelves p {
	padding: 1rem;
	max-width: 50%;
}